import { AfterContentInit, ChangeDetectionStrategy, Component, ViewChild } from '@angular/core';
import { AutoLogoutService, NavTabService } from 'mobile-money';
import { TopBarComponent } from './../top-bar/top-bar.component';

@Component({
    selector: 'vt-main',
    templateUrl: './main.component.html',
    styleUrls: ['./main.component.scss'],
    changeDetection: ChangeDetectionStrategy.Default,
})
export class MainComponent implements AfterContentInit {

    @ViewChild('topBar', {static: true}) private topBar: TopBarComponent;
    public isLoading: boolean;
    public hide: boolean;
    public allowOutsideClick: boolean;

    constructor(private navTabService: NavTabService, private autoLogoutService: AutoLogoutService) {
        if (this.autoLogoutService.getLastAction()) {
            this.autoLogoutService.check();
        }
    }

    public ngAfterContentInit(): void {
    }

    public closeMobileNav($event: MouseEvent) {
        $event.preventDefault();
    }

    public clickHide($event: any) {
        this.hide = $event;
        this.navTabService.changeSizeTab(this.hide);
    }

    public onHiddenNav($event) {
        const hamburgerBtn = document.getElementById('nav_hamburger');
        if (!hamburgerBtn.contains($event.target)) {
            this.topBar.hide = true;
            this.hide = true;
            this.navTabService.changeSizeTab(this.hide);
        }
    }

}
