import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, EventEmitter, HostListener,
     Input, OnDestroy, OnInit, Output, ViewChild, ViewRef } from '@angular/core';
import { NavTabService, slideInOut } from 'mobile-money';
import { NavigationItem } from 'mobile-money/lib/shared/common/nav-tab/store/navigation-tab';
import { Observable, Subscription } from 'rxjs';

@Component({
    selector: 'vt-sidebar',
    templateUrl: './sidebar.component.html',
    styleUrls: ['./sidebar.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
    animations: [slideInOut()],
})

export class SidebarComponent implements OnInit, OnDestroy {
    public sideNavOpen = 'in';
    @Input() public beHide: boolean;
    // tslint:disable-next-line:no-output-on-prefix
    @Output() public onHiddenNav = new EventEmitter();
    @ViewChild('input', {static: false}) public inputSearch: ElementRef;
    public vm$: Observable<NavigationItem[]>;
    public unsubcrible = new Subscription();
    public disableOutsideClick = true;

    constructor(
        private navTabService: NavTabService,
        private change: ChangeDetectorRef) {
    }

    @HostListener('window:resize', ['$event'])
    public onResize(event) {
        this.sizeChange();
    }

    public ngOnInit(): void {
        this.sizeChange();
        this.vm$ = this.navTabService.getMenuItems();
        this.navTabService.loadMenuItems();
        this.unsubcrible = this.navTabService.getMenuItems().subscribe((res) => {
            if (this.change && !(this.change as ViewRef).destroyed) {
                this.change.detectChanges();
            }
        });
    }

    public trackByFn(idx: number, item: NavigationItem) {
        return item.title + '_' + idx;
    }

    public clearTxtSearch() {
        this.inputSearch.nativeElement.value = '';
        this.navTabService.searchMenuItemsByTitle('');
    }

    /**
     * animation navside
     * @author phongnv
     */
    public toggleNavMenu() {
        return this.beHide ? 'out' : 'in';
    }

    public handleKeyup($event: KeyboardEvent, value: string) {
        this.navTabService.searchMenuItemsByTitle(value);
    }

    public clickOutside($event) {
        this.onHiddenNav.emit($event);
    }

    public sizeChange() {
        const clientWidth = document.documentElement.clientWidth;
        if (clientWidth < 1170) {
            console.log('allow outside click');
            this.disableOutsideClick = false;
        } else {
            this.disableOutsideClick = true;
        }
    }

    public ngOnDestroy(): void {
        this.unsubcrible.unsubscribe();
    }
}
