import {
    ChangeDetectorRef, Component,
    ElementRef,
    Input, OnDestroy,
    OnInit,
    ViewChild,
} from '@angular/core';
import { Router } from '@angular/router';
import { Constants, makeSlideInOut, NavTabService } from 'mobile-money';
import { NavigationItem } from 'mobile-money/lib/shared/common/nav-tab/store/navigation-tab';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
    // tslint:disable-next-line:component-selector
    selector: '[menu-nav-item]',
    templateUrl: './nav-item.component.html',
    styleUrls: ['./nav-item.component.scss'],
    animations: [makeSlideInOut()],
    // changeDetection: ChangeDetectionStrategy.OnPush
})
export class MenuNavItemComponent implements OnInit, OnDestroy {

    @Input() public item: NavigationItem;
    @Input() public isActive: boolean;
    @Input() public isOpen: boolean;
    @Input() public isSelected: boolean;
    @Input() public txtSearch: string;
    @ViewChild('itemLink', { static: false }) public itemLink: ElementRef;
    @ViewChild('itemToggle', { static: false }) public itemToggle: ElementRef;
    private unsubscribe: Subject<void> = new Subject();
    public onlyOpen: boolean;

    public lenghtMenuHidden = Constants.LENGTH_MENU_HIDDEN_TEXT;

    constructor(
        private router: Router,
        private navTabService: NavTabService,
        private change: ChangeDetectorRef,
    ) { }

    public ngOnInit(): void {
        this.navTabService.tabTrigerActive$.pipe(takeUntil(this.unsubscribe)).subscribe((res) => {
            if (res && res.item) {
                if (res.item.id === this.item.id) {
                    // if (this.item.matched) {
                        setTimeout(() => { this.itemLink.nativeElement.click(); });
                    // } else {
                        this.navTabService.addActiveTab(this.item, res.params);
                    // }
                }
                if (res.item.parentId.length) {
                    if (res.item.parentId.findIndex((item) => item === this.item.id) > -1) {
                        setTimeout(() => {
                            if (!this.isOpen) {
                                this.onlyOpen = true;
                                this.itemToggle.nativeElement.click();
                            }
                        });
                    }
                }
            }
        });
    }

    public toggleSection($event, item) {
        $event.preventDefault();
        this.navTabService.toggleMenuItems(item, item.mainMenu, this.onlyOpen);
        this.onlyOpen = false;
        this.change.detectChanges();
    }

    public trackByFn(idx: number, item: NavigationItem) {
        return item.title + '_' + idx;
    }

    public onClick(item: NavigationItem) {
        this.navTabService.addActiveTab(item);
        this.router.navigateByUrl(item.routerLink).catch(console.log);
    }

    public ngOnDestroy(): void {
        this.unsubscribe.next();
        this.unsubscribe.complete();
    }

}
